}

@media screen and (min-width: 1920px) {
	#perso {
		position: absolute;
		z-index: 1;
		height: 64px;
	}
	.parent {
		display: grid;
		grid-template-columns: repeat(9, 64px);
		grid-template-rows: repeat(9, 64px);
		grid-column-gap: 0px;
		grid-row-gap: 0px;
		border: solid 2px black;
	}
	.centre {
		display: flex;
		justify-content: center;
		/*align-items: center;*/
		grid-area: 5 / 5 / 6 / 6;
		position: relative;
		/*border: solid 1px red;*/
		width: 64px;
	}
	.message {
		grid-area: 7 / 1 / 10 / 10;
		background-color: white;
		color: black;
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 1.25em;
		border-radius: 40px;
		border: solid 2px black;
		font-family: 'Pokemon';
		/*white-space: nowrap;*/
		word-wrap: break-word;
	}
}
